import React from 'react'
import * as styles from "./index.module.css"
import Layout from "../components/layout"
import "../styles/main.css"

import face from "../images/about/face.png"
import mail from "../images/about/logos/Gmail.png"
import github from "../images/about/logos/GitHub.svg"
import linkedin from "../images/about/logos/LinkedIn.svg"
import youtube from "../images/about/logos/YouTube.svg"
import steam from "../images/about/logos/Steam.svg"

import galacticraftimage from "../images/projects/galacticraft.png"
import windowedchatimage from "../images/projects/windowed_chat.png"
import rs2019image from "../images/projects/2019_research.jpg"
import rs2018image from "../images/projects/2018_research.jpg"
import jythonimage from "../images/projects/jython_mc.gif"
import iepimage from "../images/projects/IEP.jpg"
import sf2020image from "../images/projects/2020_sciencefair.png"
import sf2019image from "../images/projects/2019_sciencefair.jpg"
import sf2018image from "../images/projects/2018_sciencefair.jpg"
import sf2017image from "../images/projects/2017_sciencefair.png"
import steveimage from "../images/projects/steve.jpg"

import ggo2020image from "../images/games/ggo2020.png"
import godotpuzzlemakerimage from "../images/games/godotpuzzlemaker.png"
import sandbox3dimage from "../images/games/sandbox3d.png"
import opentkgeimage from "../images/games/opentkgameengine.gif"
import sgvrimage from "../images/games/steins;gate-vr.png"
import chiralityimage from "../images/games/chirality.png"


const Project = props => (
    <div className={styles.projectitem}>
        <aside className={styles.projectitemaside}>
            <div className={styles.projectitemslide}>
                <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">{props.sourcelinktitle}</a></p>
            </div>
            <img className={styles.projectitemimage} src={props.image} alt={props.imagealt}/>
        </aside>
        <h1 className={styles.projectitemtitle}>{props.projecttitle}</h1>
        <h4 className={styles.projectitemsubtitle}>{props.projectsubtitle}</h4>
        <p className={styles.projectitemdescription}>{props.projectdescription}</p>
    </div>
)
const ProjectWithDisclaimer = props => (
    <div className={styles.projectitem}>
        <aside className={styles.projectitemaside}>
            <div className={styles.projectitemslide}>
                <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">{props.sourcelinktitle}</a></p>
            </div>
            <img className={styles.projectitemimage} src={props.image} alt={props.imagealt}/>
        </aside>
        <h1 className={styles.projectitemtitle}>{props.projecttitle}</h1>
        <h4 className={styles.projectitemsubtitle}>{props.projectsubtitle}</h4>
        <p className={styles.projectitemdescription}><strong>{props.disclaimer}</strong> {props.projectdescription}</p>
    </div>
)
const ProjectWithApplication = props => (
    <div className={styles.projectitem}>
        <aside className={styles.projectitemaside}>
            <div className={styles.projectitemslide} style={{height: `96px`}}>
                <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">{props.sourcelinktitle}</a></p>
                <p><a href={props.applicationlink} target="_blank" rel="noopener noreferrer">{props.applicationlinktitle}</a></p>
            </div>
            <img className={styles.projectitemimage} src={props.image} alt={props.imagealt}/>
        </aside>
        <h1 className={styles.projectitemtitle}>{props.projecttitle}</h1>
        <h4 className={styles.projectitemsubtitle}>{props.projectsubtitle}</h4>
        <p className={styles.projectitemdescription}>{props.projectdescription}</p>
    </div>
)
const ProjectWithApplicationAndDisclaimer = props => (
    <div className={styles.projectitem}>
        <aside className={styles.projectitemaside}>
            <div className={styles.projectitemslide} style={{height: `96px`}}>
                <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">{props.sourcelinktitle}</a></p>
                <p><a href={props.applicationlink} target="_blank" rel="noopener noreferrer">{props.applicationlinktitle}</a></p>
            </div>
            <img className={styles.projectitemimage} src={props.image} alt={props.imagealt}/>
        </aside>
        <h1 className={styles.projectitemtitle}>{props.projecttitle}</h1>
        <h4 className={styles.projectitemsubtitle}>{props.projectsubtitle}</h4>
        <p className={styles.projectitemdescription}><strong>{props.disclaimer}</strong> {props.projectdescription}</p>
    </div>
)
const DEFAULT_SOURCE_TITLE = "The project files can be found here."
const DEFAULT_APPLICATION_TITLE = "The mod is published on CurseForge here."
const DEFAULT_DISCLAIMER = "I am a member of a team that works on this project."


const Game = props => (
    <div className={styles.gameitem}>
        <div className={styles.gameitemslide}>
            <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">View Source</a></p>
            <p><a href={props.applink} target="_blank" rel="noopener noreferrer">Play Game</a></p>
        </div>
        <img src={props.imagesrc} alt={props.imagealt} />
        <h1>{props.title}</h1>
        <h4>{props.subtitle}</h4>
        <p>{props.description}</p>
    </div>
)
const GameWithoutApplication = props => (
    <div className={styles.gameitem}>
        <div className={styles.gameitemslide}>
            <p><a href={props.sourcelink} target="_blank" rel="noopener noreferrer">View Source</a></p>
        </div>
        <img src={props.imagesrc} alt={props.imagealt} />
        <h1>{props.title}</h1>
        <h4>{props.subtitle}</h4>
        <p>{props.description}</p>
    </div>
)


function About() {
    let date = new Date();
    let birthday = new Date(2003, 4, 14);
    let age = Math.floor((date.getTime() - birthday.getTime()) / (1000 * 60 * 60 * 24 * 365.25));
    let A = "A";
    if (age === 8 || age === 11 || age === 18 || (age >= 80 && age < 90)) {
        A += "n";
    }
    return (
        <div>
            <div className={styles.topspacer}></div>

            <div className={styles.panel}>
                <div className={styles.header}>
                    <img className={styles.faceimage} src={face} alt="My face" />
                    <p><h2>Brendan Lewis</h2><h4>@craftablescience</h4></p>
                </div>
                <p>{A} {age}-year-old programmer from the US East Coast.<br />This website is designed to quickly familiarize anyone immediately with who I am, what I enjoy doing, and some of my creations so far.<br />My resumé is available on request.</p>
                <br />
                <div className={styles.links}>
                    <a href="mailto:brendanelewis@gmail.com"><img src={mail} alt="Primary Mail" /></a>
                    <a href="https://github.com/craftablescience" target="_blank" rel="noopener noreferrer"><img src={github} alt="GitHub" /></a>
                    <a href="https://www.linkedin.com/in/brendanelewis/" target="_blank" rel="noopener noreferrer"><img src={linkedin} alt="LinkedIn" /></a>
                    <a href="https://www.youtube.com/channel/UC-w_GVUnPT9LuSm5z9eLbbQ" target="_blank" rel="noopener noreferrer"><img src={youtube} alt="YouTube" /></a>
                    <a href="https://steamcommunity.com/id/craftablescience/" target="_blank" rel="noopener noreferrer"><img src={steam} alt="Steam" /></a>
                </div>
            </div>
            <div style={{height: `25vh`}}></div>

            <div className={styles.panel}>
                <h1>Certifications &amp; Knowledge</h1>
                <div className={styles.panelcontent}>
                    <p>I have the following certifications:</p>
                    <ul>
                        <li>CompTIA A+ Certification (<a href="https://www.credly.com/badges/98006ce6-96b2-4416-8db1-89b9d9590270/public_url" target="_blank" rel="noopener noreferrer">Verify</a>)</li>
                        <li>CompTIA Network+ Certification (<a href="https://www.credly.com/badges/f8d4d32e-b46b-4d6a-a825-a7a670b5af23/public_url" target="_blank" rel="noopener noreferrer">Verify</a>)</li>
                        <li>CompTIA IT Operations Specialist Certification (<a href="https://www.credly.com/badges/082dbf14-9a6b-4413-9aa9-e9109edefc7e/public_url" target="_blank" rel="noopener noreferrer">Verify</a>)</li>
                    </ul>
                    <p>I know the following programming languages:</p>
                    <ul>
						<li>C++ (modern)</li>
                        <li>Java</li>
                        <li>C#</li>
						<li>Python 2.7 &amp; 3.x</li>
                        <li>HTML/CSS/JS (that's how I made this site)</li>
                        <li>GDScript, for the Godot Game Engine</li>
                    </ul>
                    <p>Using these languages, I have made several projects and a few games, which are further down this page. I also have some other skills:</p>
                    <ul>
                        <li>Familiar with Gradle for dependency management of Java and Kotlin projects</li>
                        <li>Familiar with Godot game engine for 2D video game creation</li>
						<li>Familiar with Source game engine for 3D video game creation</li>
                        <li>Knowledge of Blender for 3D hard surface modeling and animation</li>
                        <li>Knowledge of Minecraft modding, for Forge and Fabric modloaders</li>
                    </ul>
                </div>
            </div>
            <div style={{marginBottom: `64px`}}></div>
        </div>
    )
}

function Projects() {
    return (
        <div>
            <div style={{marginTop: `64px`}}></div>
            <ProjectWithDisclaimer
                image={galacticraftimage}
                imagealt="Galacticraft screenshot"
                projecttitle='"Galacticraft" Minecraft Mod'
                projectsubtitle="February 2019 to now"
                disclaimer={DEFAULT_DISCLAIMER}
                projectdescription="This mod aims to add other solar systems, planets, and moons to a video game called Minecraft. It is a remake of an older mod, which means it runs on newer versions of the game. I have added the sound registry code, the blockstates, models, and recipes for many blocks and items, Walkway blocks which connect to other walkways nearby, a mixin to fix particle gravity and fall damage calculations on celestial bodies with a different gravity value from the Overworld (Earth), and more."
                sourcelink="https://github.com/TeamGalacticraft/Galacticraft"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <ProjectWithApplication
                image={windowedchatimage}
                imagealt="A picture of the modification running"
                projecttitle='"Windowed Chat" Minecraft Mod'
                projectsubtitle="November 2020"
                projectdescription='This mod aims to add a new window to the video game Minecraft. Instead of having a chat box inside the game, obscuring parts of the screen, the chat is displayed in a separate window outside of the main game window. It supports all the features the regular chat does, including text formatting and clickable links. It also has a "DMs" panel, where players can send private messages to other players in a convenient UI, rather than typing "/msg PlayerNameHere message contents" and having to constantly scan the incoming chat for a reply. More features are planned in the future.'
                sourcelink="https://github.com/craftablescience/WindowedChat"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
                applicationlink="https://www.curseforge.com/minecraft/mc-mods/windowed-chat"
                applicationlinktitle={DEFAULT_APPLICATION_TITLE}
            />
            <Project
                image={rs2019image}
                imagealt="My project display board"
                projecttitle="Antivirus Database Scanner"
                projectsubtitle="Summer 2019"
                projectdescription="The aim of this project was to see the distribution of protection types between antiviruses, and measure how many threats, risks, and vulnerabilities each one had stored in their public database. The antiviruses studied were Symantec, Kaspersky, and TrendMicro. The collection of data was undertaken by a custom Python program, and the data was analyzed and charted by another Python program. This research was done for an internship at Delaware State University, under Dr. Kul, in the summer of 2019."
                sourcelink="https://github.com/craftablescience/Antivirus-Database-Scanner"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <Project
                image={rs2018image}
                imagealt="My project display board"
                projecttitle="Analyzing Spectra of LEDs"
                projectsubtitle="Summer 2018"
                projectdescription="In this project, I measured the spectra of a large amount of LEDs, for science! The LED colors were white, red, blue, yellow, and green. The data gathered was over 2 dimensions, but using Principal Component Analysis I could reduce it to a 2D scatter plot, to visualize the findings. The data shows that each LED has a slightly different spectra due to small manufacturing differences, meaning in the future they could be used as a secure physical key. This research was done for an internship at Delaware State University, under Dr. Markushin, in the summer of 2018."
                sourcelink="https://github.com/craftablescience/2018-Research-LED-Spectra"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <ProjectWithApplication
                image={jythonimage}
                imagealt="Running Python inside Minecraft"
                projecttitle='"Minecraft Python Command API" Minecraft Mod'
                projectsubtitle="August 2020"
                projectdescription="This mod aims to add a way for Python programs to run commands inside a video game called Minecraft. It allows users to run Python code from inside the game's chat or server console, scan for Python programs inside a preset directory, and run any program from the chat or console. These programs would then be able to modify the state of the game through custom functions included in the mod, like printing messages to the chat and console, or placing blocks within the world. I have created all the code for this mod, but it depends on the standalone Jython Java library to run the Python code, because the video game is written in Java."
                sourcelink="https://github.com/craftablescience/minecraft-python-cmd-api"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
                applicationlink="https://www.curseforge.com/minecraft/mc-mods/python-command-api"
                applicationlinktitle={DEFAULT_APPLICATION_TITLE}
            />
            <ProjectWithApplicationAndDisclaimer
                image={iepimage}
                imagealt="Interstellar: Exoplanets picture"
                projecttitle='"Interstellar: Exoplanets" Minecraft Mod'
                projectsubtitle="June 2020"
                disclaimer={DEFAULT_DISCLAIMER}
                projectdescription="This mod aims to add other solar systems, planets, and moons to a video game called Minecraft. It is an add-on to another mod, Galacticraft. I have added a new rocket model, and sound registry code. I am currently working on bug fixes, and waiting for the original author to request other 3D models."
                sourcelink="https://github.com/ReadOnly-Mods/Interstellar-Exoplanets"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
                applicationlink="https://www.curseforge.com/minecraft/mc-mods/interstellar-exoplanets"
                applicationlinktitle={DEFAULT_APPLICATION_TITLE}
            />
            <Project
                image={sf2020image}
                imagealt="2020 Science Fair dual camera photograph"
                projecttitle="Science Fair 2020 (VR Dual-Eye Camera Feed)"
                projectsubtitle="January 2020"
                projectdescription="My submission for the science fair in 2020 was a security device that could add a sense of depth to footage, viewable in the Oculus Quest VR headset. I used two Raspberrry Pi's with camera modules attached, to livestream their camera feeds over the Internet. Then, I created a Unity application to capture the data and relay it to each lens of the headset. Ultimately, however, it was a failure, as the two camera streams did not merge together in the headset as I had hoped. Instead it just gives the user the power to see cross-eyed."
                sourcelink="https://github.com/craftablescience/SecurityCameraWithDepth"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <Project
                image={sf2019image}
                imagealt="2019 Science Fair cat feeder image"
                projecttitle="Science Fair 2019 (Automated Cat Feeder)"
                projectsubtitle="January 2019"
                projectdescription="My submission for the science fair in 2019 was an automated cat feeder. It was powered by a LEGO motor attached to a Raspberry Pi, which turned a knob on a cheap manual cat feeder. It could be triggered automatically at a set time, or manually from an android app."
                sourcelink="https://github.com/craftablescience/Science-Fair-Projects"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <Project
                image={sf2018image}
                imagealt="2018 Science Fair list of image files used in experiment"
                projecttitle="Science Fair 2018 (Emotion Recognition)"
                projectsubtitle="January 2018"
                projectdescription="My submission for the science fair in 2018 was a program that determined the accuracy of two emotion recognition services, F.A.C.E. and SkyBiometry."
                sourcelink="https://github.com/craftablescience/Science-Fair-Projects"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <Project
                image={sf2017image}
                imagealt="2017 Science Fair picture of program"
                projecttitle="Science Fair 2017 (Reaction Times)"
                projectsubtitle="January 2017"
                projectdescription="My submission for the science fair in 2017 was a program that measured reaction times with a Raspberry Pi. It then calculated the distance a car would have travelled during that time."
                sourcelink="https://github.com/craftablescience/Science-Fair-Projects"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <Project
                image={steveimage}
                imagealt="Screenshot of code from 2016"
                projecttitle="Old Programs"
                projectsubtitle="2017 and Earlier"
                projectdescription="I lost many of my old programs to both a faulty hard drive in a laptop, and neglecting to back up my data. Fortunately, some programs were on an old flash drive from 2014, and I have uploaded them to the cloud. So far I have a prototype voice assistant Steve (who actually worked!), a weather monitor for a Raspberry Pi and SenseHAT to upload sensor data to a Dropbox folder, and two simplistic formula solver programs for the TI-84 graphing calculator."
                sourcelink="https://github.com/craftablescience/miscellaneous-old-projects"
                sourcelinktitle={DEFAULT_SOURCE_TITLE}
            />
            <div style={{marginBottom: `64px`}}></div>
        </div>
    )
}

function Games() {
    return (
        <div>
            <div className={styles.gameitemcontainer}>
                <Game
                    title="Godot Puzzlemaker"
                    subtitle="2021"
                    description="This project aims to create a quick and easy level editor for the Godot Game Engine, to make rapidly prototyping level layouts possible. No longer will modelers have to spend hours in Blender or similar 3D software! It has a save/load system, supports custom textures and entities, and exports levels to the format used in Portal 2, with many more features planned."
                    sourcelink="https://github.com/craftablescience/GodotPuzzlemaker"
                    applink="https://craftablescience.github.io/GodotPuzzlemaker"
                    imagesrc={godotpuzzlemakerimage}
                    imagealt="Godot Puzzlemaker Screenshot"
                />
                <GameWithoutApplication
                    title="OpenTK Game Engine"
                    subtitle="2021"
                    description="This was my first foray into C#. I decided to build a simple game engine using OpenGL for the graphics, Bullet for physics, and FMOD for sound. It currently can create unlit meshes, physics objects, and has several demos in a separate C# project to showcase its functionality. It also includes an OBJ mesh loader I wrote myself. All the engine features are as abstracted as possible."
                    sourcelink="https://github.com/craftablescience/OpenTKGameEngine/"
                    imagesrc={opentkgeimage}
                    imagealt="The physics demo included with the game engine"
                />
                <GameWithoutApplication
                    title="Steins;Gate VR Laboratory"
                    subtitle="2020"
                    description="This is an application for the Oculus Quest VR headset, that recreates a location from one of my favorite TV shows in VR. I created most of it at the beginning of the Covid-19 quarantine, when I was stuck at home without many major responsibilities eating away at my free time. I wanted to advance my 3D modeling skill through practicing modeling household items, and I wanted to physically be inside the laboratory in the show. This project also includes a working CRT TV, so I can watch the TV show from inside the show. I still work on it infrequently."
                    sourcelink="https://github.com/craftablescience/Steins-Gate-VR-Scene"
                    imagesrc={sgvrimage}
                    imagealt="Steins semicolon Gate laboratory model in VR"
                />
            </div>
            <div className={styles.gameitemcontainer}>
                <Game
                    title="GitHub GameOff Entry"
                    subtitle="2020"
                    description="This is a 2D game that was made for a game jam. While it was not finished, I gained a better understanding of tile maps, 2D lighting, and the node system utilized in the Godot game engine."
                    sourcelink="https://github.com/craftablescience/GithubGameOff2020"
                    applink="https://craftablescience.github.io/GithubGameOff2020"
                    imagesrc={ggo2020image}
                    imagealt="GitHub Game Off 2020 Entry Screenshot"
                />
                <Game
                    title="3D Sandbox"
                    subtitle="2019"
                    description="This is a 3D game that I once made for fun. It allows the player to run around on a featureless plane, and drive a pickup truck around."
                    sourcelink="https://github.com/craftablescience/godot-3d-sandbox"
                    applink="https://craftablescience.github.io/godot-3d-sandbox/html5"
                    imagesrc={sandbox3dimage}
                    imagealt="A picture of a very low poly red truck in front of a sunset"
                />
                <GameWithoutApplication
                    title='"Chirality"'
                    subtitle="2019"
                    description="In 2019, I was trying different game engines, to see which would be the best to use in the long term. In this project I created a title screen with Pygame, and implemented a basic tileset viewer that could view files exported from an application called Tiled. However, I decided Pygame was too slow for a commercial video game to be practical, and features that were easily accessible in other engines took hours, or even days, to implement. Based on those factors I decided to move on to other projects on other game engines."
                    sourcelink="https://github.com/craftablescience/Chirality-Game"
                    imagesrc={chiralityimage}
                    imagealt="Chirality game main menu"
                />
            </div>
        </div>
    )
}

export default function Index(props) {
    return (
        <Layout>
            {About()}
			<h1 className={styles.titleseparator}>Projects</h1>
            {Projects()}
			<h1 className={styles.titleseparator}>Games</h1>
            {Games()}
        </Layout>
    )
}
